@import url(https://fonts.googleapis.com/css?family=Special+Elite);
@import url(https://fonts.googleapis.com/css?family=Special+Elite);
@import url(https://fonts.googleapis.com/css?family=Special+Elite);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.face {
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}
.companiesImg {
  -webkit-transform: rotate(-1deg);
          transform: rotate(-1deg);
  width: 90vw;
  height: 51.5vw;
  display: inline-block;
  box-shadow: 1.5px 1.5px 8px rgba(0,0,0,0.05);
  border-radius: 5px;
}
.resume {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.resume p, .resume h2 {
  text-align: left;
  margin: 1em;
}
#titleName {
  color: #133b9e;
  font-size: 1.5em;
  font-weight: bold;
}
.resumeImg {
  cursor: pointer;
  transition: 0.175s;
  width: 40vw;
}
.resumeImages {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
.resumeImg:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.stickyNote{
  background: transparent url(/static/media/Sticky_Note.0d34e106.png) top center no-repeat;
  background-size: contain;
  text-align: left;
  display: flex;
  justify-content: center;
  min-width: 55vw;
  min-height: 40vw;
}
.stickyNote ul {
  list-style: none;
  padding-left: 0;
}
.stickyNote li {
  padding-bottom: 0.5em;
}
.stickyNote a {
  color: #000;
}
.stickyNote a:hover {
  color: #777;
}

@media only screen and (min-width: 700px) {
  .companiesImg {
    width: 50vw;
    height: 28.5vw;
    margin-top: 3vh;
  }
  .resume {
    width: 80vw;
  }
  .resumeImg {
    width: 20vw;
  }
  .stickyNote {
    min-width: 28vw;
    min-height: 28vw;
  }
}

@media only screen and (min-width: 1200px) {

  .resume {
    flex-direction: row;
    text-align: left;
    justify-content: space-around;
  }
  .resume > div:first-child {
    width: 45vw;
  }
  .resume > div:first-child > img {
    margin: 0 auto;
    display: block;
  }
  .companiesImg {
    width: 26.65vw;
    height: 15.25vw;
    margin: 2vw;
  }
  .resumeImg {
    width: 10vw;
  }
  .stickyNote {
    min-width: 16vw;
    min-height: 16vw;
  }
}

h2, a {
  color: #133b9e;
}
h2 {
  margin: 0;
}
a {
  font-weight: 700;
}
a:hover {
  color: #000;
}
.drawerBack h3 {
  display: inline-block;
  margin-top: 1.5em;
  width: 100%;
}
.drawerBack a {
  font-size: 0.85em;
}
.relatedProjects {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.relatedProjects div {
  cursor: pointer;
  color: #fff;
  font-family: "Special Elite";
  width: 40%;
  height: 3em;
  text-align: center;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.relatedProjects div:hover, .relatedProjects div:active {
  color: #133b9e;
}
.projectWrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.projectWrap p {
  width: 100%;
}
.relatedProjects hr {
  background: transparent url(/static/media/HorizontalRule.a1dc6654.png) center no-repeat;
  background-size: contain;
  width: 90%;
  height: 1em;
  border: 0px;
  margin: 2em 0;
}
.drawerBack .projectsTitle {
  width: 100%;
  text-align: center;
  font-weight: 400;
  padding: 1vh;
  margin-top: inherit;
  margin-bottom: 1.5em;
}
.projectImage {
  width: 45%;
  border: 10px solid #fff;
  border-bottom: 20px solid #fff;
  box-shadow: 1.5px 1.5px 8px rgba(0,0,0,0.05);
  background: #fff;
  text-align: center;
  font-family: "Special Elite";
}
.projectImage > img {
  width: 100%;
  margin-bottom: 12.5px;
}

.drawerBack {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 40px auto;
  max-width: 90%;
}
.drawerBack video {
  width: 100%;
}
.drawerBack .video {
  position: relative;
  cursor: pointer;
}
.drawerBack .video:after {
  content: '';
  width: 102%;
  height: 102%;
  position: absolute;
  top: -1.05%;
  left: -.95%;
  background: transparent url(/static/media/Video_Border.9fc96bab.png) no-repeat;
  background-size: contain;
  pointer-events: none;
  /*filter: drop-shadow(1px 1px 3px rgba(0,0,0,.25));
  clip-path: polygon(0% 1%, 100% 1%, 100% 98%, 0% 98%);*/
}

.playButton {
  width: 30%;
  height: 47%;
  position: absolute;
  top: 25%;
  left: 35%;
  background: transparent url(/static/media/Video_Play.21c3c809.png) no-repeat;
  background-size: contain;
  -webkit-filter: drop-shadow(1px 1px 3px rgba(0,0,0,.25));
          filter: drop-shadow(1px 1px 3px rgba(0,0,0,.25));
}
.playButton:not(.hide):hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.resetVideo {
  width: 45px;
  height: 45px;
  position: absolute;
  bottom: 10%;
  right: 7%;
  background: transparent url(data:image/png;base64,) no-repeat;
  background-size: contain;
  -webkit-filter: drop-shadow(1px 1px 3px rgba(0,0,0,.25));
          filter: drop-shadow(1px 1px 3px rgba(0,0,0,.25));
  opacity: 0;
}
.resetVideo:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}
.video .resetVideo:hover + .playButton {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.show {
  opacity: 1;
  transition: opacity 400ms;
}
.hide {
  opacity: 0;
  transition: opacity 400ms;
}

.modalClass {
  position: absolute;
  top: 30px;
  background-image: url(/static/media/White_Paper.b02095ba.png);
  background-color: #ecedea;
  background-repeat: repeat-y;
	background-size: 100vw 100vh;
  width: 100%;
  max-width: 700px;
  min-height: 100%;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.closeButton {
  position: absolute;
  top: 0;
  right: 0;
  width: 45px;
  height: 45px;
  font-size: 1.75em;
  color: #444;
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  font-family: "Special Elite";
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .drawerBack {
    border-radius: 0;
  }
  .closeButton:hover {
    color: #000;
  }
}

@media (max-width: 767px) {
  .modalClass {
    width: 100%;
    border-radius: 0px;
  }
  .closeButton {
    width: 50vw;
    right: auto;
    color: rgba(0,0,0,0);
    border-bottom: 2px solid #444;
    height: 20px;
  }
}

.project-link-wrap {
  list-style: none;
  font-family: "Special Elite";
  color: #fff;
  font-size: 2em;
  display: flex;
  justify-content: space-around;
  width: 75vw;
  margin: 1em auto;
  padding: 0;
  text-transform: uppercase;
  flex-wrap: wrap;
}
.project-link-wrap li {
  padding: 1.75vh 1vw 0.5vh;
  font-size: calc(10px + 3vmin);
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  background: #000;
  margin: 0.5vh;
}
.project-link-wrap li:hover {
  color: #133b9e;
}
.project-link-wrap li.active {
  color: #133b9e;
  cursor: default;
}
.projectList {
  list-style: none;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 90vw;
  margin: 1em auto;
  padding: 0;
}
.projectList li {
  min-width: 100px;
  width: 35vw;
  height: 25vw;
  margin: 2vw;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: 0.1s;
}
.projectList li:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

@media only screen and (min-width: 700px) {
  .projectList {
    width: 80vw;
  }
  .projectList li {
    min-width: 150px;
    width: 20vw;
    height: 15vw;
    margin: 1vw;
  }
}

@media only screen and (min-width: 1200px) {
  .projectList {
    width: 65vw;
  }
  .project-link-wrap {
    width: 65vw;
  }
  .projectList li {
    min-width: 150px;
    width: 15vw;
    height: 15vw;
    margin: 1vw;
  }
}

